.AwesomeGridLayoutRoot {
    pointer-events: auto;
    -ms-overflow-style: none;
}
.AwesomeGridLayoutGrid {
    display: grid;
}
.AwesomeGridLayoutHolder {
    display: grid;
    width: 100%;
    height: 100%;
    position: relative;
}
.AwesomeGridLayoutCursorMove {
    cursor: move;
}
.AwesomeGridLayoutRootHover {
    pointer-events: none;
    box-shadow: rgb(255, 0, 0) 0 0 0 1px;
    transform: translateY(100%);
}
.AwesomeGridLayoutRoot::-webkit-scrollbar {
    display: none;
}
/*TODO add firefox support*/

.AwesomeGridLayoutChild {
    position: absolute;
}
.AGLAnchor{
    position: absolute;
    top: 0;
}
